<template>
  <div class="login-page">
    <a-form :form="form" @submit="handleSubmit" style="width: 360px">
      <h1></h1>
      <a-form-item>
        <a-input
          v-focus
          v-decorator="[
            'account',
            {
              rules: [{ required: true, message: '' }]
            }
          ]"
          placeholder=""
          size="large"
        >
          <a-icon slot="prefix" type="user" />
          <a-popover style="cursor: pointer" slot="suffix">
            <template slot="content">
               
            </template>
            <a-icon type="question-circle" />
          </a-popover>
        </a-input>
      </a-form-item>
      <a-form-item>
        <a-input
          v-decorator="[
            'password',
            {
              rules: [{ required: true, message: '' }]
            }
          ]"
          size="large"
          type="password"
          placeholder=""
        >
          <a-icon slot="prefix" type="lock" />
          <a-popover style="cursor: pointer" slot="suffix">
            <template slot="content">
               
            </template>
            
            <a-icon type="question-circle" />
          </a-popover>
        </a-input>
      </a-form-item>
      <a-form-item>
        <a-input
          v-decorator="[
            'address',
            {
              rules: [{ required: true, message: '' }]
            }
          ]"
          size="large"
          placeholder=" z/OSMF "
          @change="onAddressChange"
        >
          <a-icon slot="prefix" type="cloud" />
        </a-input>
      </a-form-item>
      <a-form-item>
        <a-button
          type="primary"
          html-type="submit"
          size="large"
          block
          :loading="isLoading"
          ></a-button
        >
      </a-form-item>
      <a-popover style="cursor: pointer">
        <template slot="content">
          <p>
             z/OSMF <a
              :href="`https://${this.address}`"
              target="_blank"
              >[  ]</a
            >
          </p>
          <p>
             z/OSMF<a
              :href="`https://${this.address}/zosmf`"
              target="_blank"
              >[  ]</a
            >
          </p>
          <p> z/OSMF </p>
           TSO 
        </template>
        
        <a-icon type="question-circle" />
      </a-popover>
    </a-form>
  </div>
</template>

<script>
import Axios from "axios";
import { Modal } from "ant-design-vue";
// remove warning of async-validator used by a-form
// ref: https://github.com/yiminghe/async-validator#how-to-avoid-warning
import Schema from "async-validator";
Schema.warning = function() {};

export default {
  name: "LoginForm",
  data() {
    return {
      form: this.$form.createForm(this),
      isLoading: false,
      address: ""
    };
  },
  mounted() {
    this.form.setFieldsValue({
      address: "10.60.43.8:8800"
    });
    this.address = "10.60.43.8:8800";
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields(async err => {
        if (err) return;
        this.doLogin();
      });
    },

    async doLogin() {
      this.isLoading = true;

      try {
        const user = this.form.getFieldsValue();
        const response = await Axios.post("/api/login", user);
        switch (+response.status) {
          case 200: {
            const loginState = await Axios.get("/api/login");
            await this.$store.dispatch("user/login", loginState.data);
            return;
          }
          case 401:
            Modal.error({
              title: "",
              content: "",
              centered: true
            });
        }
      } catch (error) {
        if (error.message.includes("401")) {
          Modal.error({
            title: "",
            content: "",
            centered: true
          });
        } else {
          Modal.error({
            title: "",
            content: error.message,
            centered: true
          });
        }
      } finally {
        this.isLoading = false;
      }
    },
    onAddressChange(e) {
      if (e.target.value) {
        this.address = e.target.value;
      } else {
        this.address = "z/OSMF";
      }
    }
  }
};
</script>
<style scoped>
h1 {
  text-align: center;
}
</style>
