<template>
  <div class="menu-container">
    <a-menu
      theme="dark"
      mode="inline"
      @click="handleMenuClick"
      v-model="selectedKeys"
    >
      <a-menu-item
        v-for="menu in menuConfig"
        :key="
          (menu.path && '/administration/' + menu.path) || '/administration'
        "
      >
        <a-icon :type="menu.icon" />
        <!--  span  -->
        <span>{{ menu.name }}</span>
      </a-menu-item>
    </a-menu>
  </div>
</template>

<script>
export default {
  name: "SidebarMenu",
  data() {
    return {
      selectedKeys: [this.$route.path],
      menuConfig: [
        {
          name: "",
          path: "",
          icon: "home"
        },
        {
          name: "RACF",
          path: "racf",
          icon: "safety"
        },
        {
          name: "SMS",
          path: "sms",
          icon: "hdd"
        },
        {
          name: "Catalog",
          path: "catalog",
          icon: "inbox"
        },
        {
          name: "MVS",
          path: "mvs",
          icon: "code"
        },
        {
          name: "REXX",
          path: "rexx",
          icon: "snippets"
        }
      ]
    };
  },
  computed: {
    path() {
      return this.$route.path;
    }
  },
  watch: {
    path(v) {
      this.selectedKeys = [v];
    }
  },
  methods: {
    handleMenuClick(e) {
      if (e.key) this.$router.push(e.key);
      else this.$router.push("/administration");
    }
  }
};
</script>

<style scoped>
.menu-container {
  height: 92%;
  overflow: auto;
}
</style>
